<template>
  <div class="list">
    <ul>
      <li v-for="item in list" :key="item.id">
        <div class="left">
          <div>{{ item.way }}</div>
          <div>{{ item.time }}</div>
        </div>
        <div :class="{number:true,red:red}">+{{ item.add }}</div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props:['list','red']
};
</script>

<style lang="scss" scoped>
.list {
  ul {
    li {
    color: #222;
    font-size: 28px;
    height: 120px;
    padding: 0 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    border-bottom: thin solid #e5e5e5;
    &:last-of-type {
      border: none;
    }
    .left {
      & > div:nth-of-type(2) {
        color: #888;
        font-size: 24px;
        margin-top: 5px;
      }
    }
    .number {
      color: #d2ae55;
      font-size: 32px;
      &.red{
        color: #FF4A54;
      }
    }
  }
  } 
}
</style>



