<template>
  <div class="content">
    <div class="header">
      <div class="money">
        ￥
        <span>1202.00</span>
      </div>
      <p>累计收益</p>
      <div class="line"></div>
      <div class="title">
        <div>
          <span></span>
          收益明细
        </div>
      </div>
    </div>
    <div class="item-wrap">
      <div class="income-list">
        <list-details :list="integralList" :red="true" />
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          :error.sync="error"
          error-text="点击重新加载"
          :offset="100"
        ></van-list>
      </div>
    </div>
  </div>
</template>

<script>
import listDetails from "@/web/components/ListDetails";
export default {
  components: {
    listDetails
  },
  data() {
    return {
      loading: true, //加载
      finished: false, //是否加载完成
      error: false, //加载错误

      integralList: [
        {
          id: 1,
          way: "推荐 13680311947注册成功",
          time: "昨天 13:46:47",
          add: "10.00"
        },
        {
          id: 2,
          way: "推荐 13680311947注册成功",
          time: "2019-01-29 13:46:47",
          add: "10.00"
        },
        {
          id: 3,
          way: "团队消费奖",
          time: "2019-01-29 13:46:47",
          add: "10.00"
        },
        {
          id: 4,
          way: "团队消费奖",
          time: "2019-01-29 13:46:47",
          add: "12.00"
        },
        {
          id: 5,
          way: "推荐 13680311947注册成功",
          time: "2019-01-29 13:46:47",
          add: "10.00"
        },
        {
          id: 6,
          way: "推荐 13680311947注册成功",
          time: "2019-01-29 13:46:47",
          add: "5.00"
        },
        {
          id: 7,
          way: "团队消费奖",
          time: "2019-01-29 13:46:47",
          add: "8.00"
        },
        {
          id: 9,
          way: "团队消费奖",
          time: "2019-01-29 13:46:47",
          add: "10.00"
        },
        {
          id: 10,
          way: "团队消费奖",
          time: "2019-01-29 13:46:47",
          add: "20.00"
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
.content {
  .header {
    position: fixed;
    left: 0;
    top: 0;
    height: 280px;
    width: 100%;
    background: url("../../../assets/personal/bg-income.png") no-repeat;
    background-size: 100% 100%;
    text-align: center;
    color: #fff;
    // overflow: hidden;
    // position: relative;
    .money {
      font-size: 36px;
      margin-top: 55px;
      span {
        font-size: 56px;
      }
    }
    p {
      font-size: 24px;
    }
    .line {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      width: 96%;
      height: 20px;
      background-color: #ffffff;
      box-shadow: 0px 2px 6px rgba(201, 201, 201, 1);
      border-radius: 10px;
      bottom: 40px;
      z-index: 999;
    }
    .title {
      
      
      font-size: 30px;
      color: #222;
      
      border-bottom: thin solid #e5e5e5;
			width: 100%;
      position: absolute;
	    padding: 0 24px;
      bottom: -59px;
      z-index: 888;
      box-sizing: border-box;
			&>div{
				background-color: #fff;
				padding: 40px 24px 0 48px;
				height: 115px;
				width: 100%;
				display: flex;
				box-sizing: border-box;
      	align-items: center;
				span {
        width: 6px;
        height: 26px;
        display: block;
        background-color: #FD656D;
        margin-right: 20px;
      }
			}
      
    }
  }

  .item-wrap {
    padding: 0 25px;
    width: 100%;
    height: auto;
    overflow: auto;
    position: fixed;
    z-index: 2;
    bottom: 0;
    top: 4.506667rem;
    box-sizing: border-box;
  }
  .income-list {
    // position: relative;
    // top: -50px;
    width: 100%;
    // margin: 0 25px 0;
    background-color: #fff;
    z-index: 1;
  }
}
</style>


